<template>
    <div>
        <div class="container">
            <div class="row">
                <div class="col s12 m8 offset-m2">
                    <div class="login card-panel grey lighten-4 black-text center">
                        <img src="./logo2.png" height="300px">
                        <h3>Criar conta</h3>
                        <form>
                            <div class="input-field">
                                <i class="material-icons prefix">email</i>
                                <input type="text" id="email" v-model="email">
                                <label for="email">Email</label>
                            </div>
                            <div class="input-field">
                                <i class="material-icons prefix">lock</i>
                                <input type="password" id="password" v-model="password">
                                <label for="password">Password (Mín. 6 caracteres)</label>
                            </div>
                            <button v-on:click="register" class="btn btn-large grey lighten-4 black-text">Criar</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import firebase from 'firebase'
export default {
    name: 'register',
    data: function () {
        return{
            email: '',
            password: '',
        };
    },
    methods: {
        register: function(e) {
            firebase.auth().createUserWithEmailAndPassword(this.email, this.password,).then(user => {
                alert('Conta criada com sucesso');
                this.$router.go({path: this.$router.path});
            },
            err => {
                alert('Conta não foi criada com sucesso. Tente novamente')
            });

            e.preventDefault();
        }
    }
};
</script>




