import Vue from 'vue'
import Router from 'vue-router'
import Cacifos from '@/components/Cacifos'
import NovoCacifo from '@/components/NovoCacifo'
import VerCacifo from '@/components/VerCacifo'
import EditarCacifo from '@/components/EditarCacifo'
import Login from '@/components/Login'
import Register from '@/components/Register'
import firebase from 'firebase'

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'cacifos',
      component: Cacifos,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/criar',
      name: 'register',
      component: Register,
      meta: {
        requiresGuest: true
      }
    },
    {
      path: '/novo',
      name: 'novo-cacifo',
      component: NovoCacifo,
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/editar/:cacifo_id',
      name: 'editar-cacifo',
      component: EditarCacifo,
      meta: {
        requiresAuth: true
      }
    },{
      path: '/:cacifo_id',
      name: 'ver-cacifo',
      component: VerCacifo,
      meta: {
        requiresAuth: true
      }
    },
  ]
});

// Nav Guards

router.beforeEach((to, from, next) => {
  //verificar o requiresAuth guard
  if(to.matched.some(record => record.meta.requiresAuth)){
    //ver se NÃO esta loggado
    if(!firebase.auth().currentUser){
      //vai para o login
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
    }else{
      //ir para o route
      next();
    }
  } else if(to.matched.some(record => record.meta.requiresGuest)) {
    //ver se está loggado
    if(firebase.auth().currentUser){
      //vai para o login
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      });
    }else{
      //ir para o route
      next();
    }
  } else {
    //ir para o route
    next();
  }
});

export default router;



