<template>
    <div id="editar-cacifo">
        <h3>Editar Cacifo</h3>
        <div class="row">
            <form @submit.prevent="updateLocker" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input  disabled type="text" v-model="cacifo_id" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="nomeAluno">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="turmaAluno">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="numeroAluno">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="localizacao">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="estado">
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="chave">
                    </div>
                </div>
                <button type="submit" class="btn green">Submeter</button>
                <router-link to="/" class="btn grey">Cancelar</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: "editar-cacifo",
    data() {
        return{
            cacifo_id: null,
            chave: null,
            estado: null,
            localizacao: null,
            nomeAluno: null,
            numeroAluno: null,
            turmaAluno: null,
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('cacifos').where('cacifo_id', '==', to.params.cacifo_id).get().then(querySnapshot =>{
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.cacifo_id = doc.data().cacifo_id
                    vm.chave = doc.data().chave
                    vm.estado = doc.data().estado
                    vm.localizacao = doc.data().localizacao
                    vm.nomeAluno = doc.data().nomeAluno
                    vm.numeroAluno = doc.data().numeroAluno
                    vm.turmaAluno = doc.data().turmaAluno
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('cacifos').where('cacifo_id', '==', this.$route.params.cacifo_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.cacifo_id = doc.data().cacifo_id
                    this.chave = doc.data().chave
                    this.estado = doc.data().estado
                    this.localizacao = doc.data().localizacao
                    this.nomeAluno = doc.data().nomeAluno
                    this.numeroAluno = doc.data().numeroAluno
                    this.turmaAluno = doc.data().turmaAluno  
                })
            })
        },
        updateLocker () {
            db.collection('cacifos').where('cacifo_id', '==', this.$route.params.cacifo_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        cacifo_id: this.cacifo_id,
                        chave: this.chave,
                        estado: this.estado,
                        localizacao: this.localizacao,
                        nomeAluno: this.nomeAluno,
                        numeroAluno: this.numeroAluno,
                        turmaAluno: this.turmaAluno
                    })
                    .then(() => {
                        this.$router.push({name: 'ver-cacifo', params: {cacifo_id: this.cacifo_id}})
                    })
                })
            })
        }
    }
}
</script>



