<template>
    <div id="novo-cacifo">
        <h3>Novo Cacifo</h3>
        <div class="row">
            <form @submit.prevent="saveLocker" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="cacifo_id" required>
                        <label>Número do Cacifo</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="nomeAluno">
                        <label>Nome do Aluno</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="turmaAluno">
                        <label>Turma do Aluno</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="numeroAluno">
                        <label>Número do Aluno</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="localizacao" required>
                        <label>Localização</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="estado" required>
                        <label>Estado</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="chave" required>
                        <label>Chave</label>
                    </div>
                </div>
                <button type="submit" class="btn green">Submeter</button>
                <router-link to="/" class="btn grey">Cancelar</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: "novo-cacifo",
    data() {
        return{
            cacifo_id: null,
            chave: null,
            estado: null,
            localizacao: null,
            nomeAluno: null,
            numeroAluno: null,
            turmaAluno: null
        }
    },
    methods: {
        saveLocker () {
            db.collection('cacifos').add({
                cacifo_id: this.cacifo_id,
                chave: this.chave,
                localizacao: this.localizacao,
                estado: this.estado,
                nomeAluno: this.nomeAluno,
                numeroAluno: this.numeroAluno,
                turmaAluno: this.turmaAluno,
            })
            .then(docRef => this.$router.push('/'))

            .catch(error => console.log(err))
        }
    }
}
</script>


