<template>
    <div id="ver-cacifo">
        <ul class="collection with-header">
            <li class="collection-header"><h4>{{cacifo_id}}</h4>
            </li>
            <li class="collection-item">Nome do Aluno: {{nomeAluno}}</li>
            <li class="collection-item">Turma do Aluno:  {{turmaAluno}}</li>
            <li class="collection-item">Número do Aluno: {{numeroAluno}}</li>
            <li class="collection-item">Localização: {{localizacao}}</li>
            <li class="collection-item">Estado: {{estado}}</li>
            <li class="collection-item">Chave: {{chave}}</li>
            
        </ul>
        <router-link to="/" class="btn grey"> Voltar</router-link>
        <button @click="deleteLocker" class="btn red"> Apagar</button>
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name:  'editar-cacifo', params: {cacifo_id: cacifo_id}}" class="btn-floating btn-large red">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: "ver-cacifo",
    data() {
        return{
            cacifo_id: null,
            nomeAluno: null,
            turmaAluno: null,
            numeroAluno: null,
            localizacao: null,
            estado: null,
            chave: null
        }
    },
    beforeRouteEnter (to, from, next) {
        db.collection('cacifos').where('cacifo_id', '==', to.params.cacifo_id).get().then(querySnapshot =>{
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.cacifo_id = doc.data().cacifo_id
                    vm.nomeAluno = doc.data().nomeAluno
                    vm.turmaAluno = doc.data().turmaAluno
                    vm.numeroAluno = doc.data().numeroAluno
                    vm.localizacao = doc.data().localizacao
                    vm.estado = doc.data().estado
                    vm.chave = doc.data().chave

                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData () {
            db.collection('cacifos').where('cacifo_id', '==', this.$route.params.cacifo_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.cacifo_id = doc.data().cacifo_id
                    this.nomeAluno = doc.data().nomeAluno
                    this.turmaAluno = doc.data().turmaAluno
                    this.numeroAluno = doc.data().numeroAluno
                    this.localizacao = doc.data().localizacao
                    this.estado = doc.data().estado
                    this.chave = doc.data().chave
                })
            })
        },
        deleteLocker () {
            if(confirm('Tem a certeza?')) {
                db.collection('cacifos').where('cacifo_id', '==', this.$route.params.cacifo_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete()
                    this.$router.push('/')
                })
            })
            }
        }
    }
}
</script>


