<template>
    <div id="cacifos">
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Cacifos 
                    <input type="text" v-model="search" placeholder="Pesquisa"/>
                </h4>
            </li>
            <li v-for="cacifo in filteredLockers" v-bind:key="cacifo.id" class="collection-item">
                <div class="chip">{{cacifo.cacifo_id}}</div>{{cacifo.nomeAluno}} : {{cacifo.numeroAluno}} : {{cacifo.turmaAluno}} : {{cacifo.localizacao}} : {{cacifo.chave}} : {{cacifo.estado}}
                <router-link class="secondary-content" v-bind:to="{name: 'ver-cacifo', params: {cacifo_id: cacifo.cacifo_id}}">
                    <i class="fa fa-eye"></i>
                </router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/novo" class="btn-floating btn-large red">
                <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: "cacifos",
    data() {
        return {
            cacifos: [],
            search: ''
        }
    },
    created () {
        db.collection('cacifos').orderBy('cacifo_id').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'cacifo_id': doc.data().cacifo_id,
                    'chave': doc.data().chave,
                    'estado': doc.data().estado,
                    'localizacao': doc.data().localizacao,
                    'nomeAluno': doc.data().nomeAluno,
                    'numeroAluno': doc.data().numeroAluno,
                    'turmaAluno': doc.data().turmaAluno,
                }
                this.cacifos.push(data)
            })
        })
    },
    computed: {
        filteredLockers: function() {
            return this.cacifos.filter((cacifo) => {
                // return cacifo.cacifo_id.match(this.search)
                return cacifo.nomeAluno.match(this.search)
                // return cacifo.numeroAluno.match(this.search)
                // return cacifo.turmaAluno.match(this.search)
                // return cacifo.localizacao.match(this.search)
                // return cacifo.estado.match(this.search)
                // return cacifo.chave.match(this.search)
            })
        },
    }

}
</script>


